<template>
    <div class="vue-doc-wrapper">
        <div class="vue-doc-submenu">
          <ul class='vue-doc-submenu-list'><li><a href="#vue-doc-基础用法1">基础用法</a></li><li><a href="#vue-doc-提醒类型1">提醒类型</a></li><li><a href="#vue-doc-自定义时长1">自定义时长</a></li><li><a href="#vue-doc-Render函数1">Render 函数</a></li></ul><ul class='vue-doc-submenu-list-EN'><li><a href="#vue-doc-ButtonSize1">Button Size</a></li><li><a href="#vue-doc-ButtonSize1">Button Size</a></li><li><a href="#vue-doc-ButtonSize1">Button Size</a></li><li><a href="#vue-doc-ButtonSize1">Button Size</a></li></ul>
        </div>
        <div class="vue-doc-title">
          <h1 id="vue-doc-notice通知提醒1" class="vue-doc-element">Notice 通知提醒</h1>
<p class="vue-doc-element">在界面右上角显示可关闭的全局通知。</p>

        </div>
        <div class="vue-doc-title-EN">
          <h1 id="vue-doc-button1" class="vue-doc-element">Button</h1>
<p class="vue-doc-element">Basic component that is used to trigger bussiness logic.</p>

        </div>

        <div class="vue-doc-components">
          <demobasic></demobasic>
<demotype></demotype>
<demotime></demotime>
<demorender></demorender>
        </div>


        <div class="vue-doc-content">
          <h2 id="vue-doc-api1" class="vue-doc-element">API</h2>
<h3 id="vue-doc-noticeinstance1_1" class="vue-doc-element">Notice instance</h3>
<p class="vue-doc-element">通过直接调用以下方法来使用组件：</p>
<ul class="vue-doc-element">
<li><code class="vue-doc-element">this.$Notice.open(config)</code></li>
<li><code class="vue-doc-element">this.$Notice.info(config)</code></li>
<li><code class="vue-doc-element">this.$Notice.success(config)</code></li>
<li><code class="vue-doc-element">this.$Notice.warning(config)</code></li>
<li><code class="vue-doc-element">this.$Notice.error(config)</code>
以上方法隐式地创建及维护Vue组件。参数 config 为对象，具体说明如下：</li>
</ul>
<table class="vue-doc-element">
<thead>
<tr>
<th>属性</th>
<th>说明</th>
<th>类型</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>title</td>
<td>通知提醒的标题</td>
<td>String</td>
<td>-</td>
</tr>
<tr>
<td>desc</td>
<td>通知提醒的内容，为空或不填时，自动应用仅标题模式下的样式</td>
<td>String</td>
<td>-</td>
</tr>
<tr>
<td>render</td>
<td>自定义描述内容，使用 Vue 的 Render 函数，如果同时设置了 render 和 desc，则只显示 render 的内容</td>
<td>Function</td>
<td>-</td>
</tr>
<tr>
<td>duration</td>
<td>自动关闭的延时，单位秒，不关闭可以写 0</td>
<td>Number</td>
<td>4.5</td>
</tr>
<tr>
<td>name</td>
<td>当前通知的唯一标识</td>
<td>String</td>
<td>自动</td>
</tr>
<tr>
<td>onClose</td>
<td>关闭时的回调</td>
<td>Function</td>
<td>-</td>
</tr>
</tbody>
</table>
<p class="vue-doc-element">另外提供了全局配置、全局关闭某个通知和全局销毁的方法：</p>
<ul class="vue-doc-element">
<li><code class="vue-doc-element">this.$Notice.config(options)</code></li>
<li><code class="vue-doc-element">this.$Notice.close(name)</code></li>
<li><code class="vue-doc-element">this.$Notice.destroy()</code><pre class="vue-doc-element"><code class="lang-js">this.<span class="hljs-variable">$Notice</span><span class="hljs-selector-class">.config</span>({
  <span class="hljs-attribute">top</span>: <span class="hljs-number">50</span>,
  duration: <span class="hljs-number">3</span>
});
</code></pre>
</li>
</ul>
<table class="vue-doc-element">
<thead>
<tr>
<th>属性</th>
<th>说明</th>
<th>类型</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>top</td>
<td>通知组件距离顶端的距离，单位像素</td>
<td>Number</td>
<td>24</td>
</tr>
<tr>
<td>duration</td>
<td>默认自动关闭的延时，单位秒</td>
<td>Number</td>
<td>4.5</td>
</tr>
</tbody>
</table>

        </div>
        <div class="vue-doc-content-EN">
          <h2 id="vue-doc-api1" class="vue-doc-element">API</h2>
<table class="vue-doc-element">
<thead>
<tr>
<th>Attribute</th>
<th style="text-align:right">Explanation</th>
<th>Type</th>
<th>Default</th>
</tr>
</thead>
<tbody>
<tr>
<td>type</td>
<td style="text-align:right">Button type, options include primary、ghost、dashed、text、info、success、warning、error, optional</td>
<td>String</td>
<td>-</td>
</tr>
</tbody>
</table>

        </div>
        


    </div>
  </template>
  <script>
      
  import demobasic from './demo/basic.md';
import demotype from './demo/type.md';
import demotime from './demo/time.md';
import demorender from './demo/render.md';
  import ClipboardJS from "clipboard";
  const mixin = {
    data(){
      return{
        showVueDocCode:false,
        vueDocCodeCopyText:"copy",
        vueDocCodeHeight:0
      }
    },
    methods: {
      vueDocCodeToggle() {
        this.showVueDocCode=!this.showVueDocCode;
        this.vueDocCodeHeight=this.vueDocCodeHeight||this.$refs.vueDocCodeBox.children[0].offsetHeight;
        this.$refs.vueDocCodeBox.style.height=this.showVueDocCode?(this.vueDocCodeHeight+50)+'px':'0px';
      },
      vueDocCodeCopy(){
        const clipboard = new ClipboardJS('.vue-doc-code-copy',{
          text:(trigger)=> {
                return trigger.nextElementSibling.textContent;
            }
        });
        clipboard.on('success', (e)=> {
            this.vueDocCodeCopyText="copy success!";
            setTimeout(()=>{
              this.vueDocCodeCopyText="copy";
            },1000);
            e.clearSelection();
            clipboard.destroy();
        });
      }
    },
    components:{
      demobasic,demotype,demotime,demorender
    }
  };
  
      export default {
        mixins:[mixin]
      }
      </script>
  
  <style lang="less">
    @import "ivue-doc-loader/styles/code/default.css";
    @import "ivue-doc-loader/styles/default.css";
    .vue-doc-code-content{
      height:0;
      overflow:hidden;
      transition: height .3s;
      background:#f8f8f8;
    }
    .vue-doc-code-show .vue-doc-code-content{
      height:auto;
    }
    .vue-doc-content-EN,.vue-doc-title-EN,.vue-doc-submenu-list-EN,.vue-doc-code-toggle-text-EN{
      display:none;
    }
    .en-US .vue-doc-submenu-list-EN{
      display:block;
    }
    .en-US .vue-doc-submenu-list{
      display:none;
    }

    .en-US .vue-doc-content-EN{
      display:block;
    }
    .en-US .vue-doc-content{
      display:none;
    }

    .en-US .vue-doc-title-EN{
      display:block;
    }
    .en-US .vue-doc-title{
      display:none;
    }

    .en-US .vue-doc-code-toggle-text-EN{
      display:block;
    }
    .en-US .vue-doc-code-toggle-text{
      display:none;
    }

    .vue-doc-demo{
      padding: 20px;
      border:1px solid #ddd;
      border-bottom: 0;
      border-top-left-radius: 5px;
      border-top-right-radius: 5px;
    }
    .vue-doc-code{
      border: 1px solid #ddd;
      position: relative;
      padding-bottom: 50px;
      border-bottom-left-radius: 5px;
      border-bottom-right-radius: 5px;
    }
    .vue-doc-code-copy{
      display: none;
      position: absolute;
      right: 15px;
      font-weight: bold;
      top: 10px;
      cursor: pointer;
    }
    .vue-doc-code-show .vue-doc-code-copy{
      display: block;
    }
    .vue-doc-code-toggle{
      position: absolute;
      bottom: 0;
      left: 0;
      width:100%;
      font-weight: bold;
      height: 50px;
      line-height: 50px;
      text-align: center;
      cursor: pointer;
    }
    .vue-doc-code-show .vue-doc-code-toggle{
      border-top: 1px solid #ddd;
    }

    .vue-doc-code .vue-doc-code-content pre{
      border: 0;
      padding: 15px;
    }

    .vue-doc-wrapper{
      margin-bottom:50px;
    }



  </style>


